import React from "react";

const ProductCard = () => {
    const displayProducts = JSON.parse(localStorage.getItem("products"));
  return (
    <div className="Prod">
    {!displayProducts ? <h1>No product Added</h1> : 
        <div> 
        {displayProducts.map((proObj, index) => (
            <div className="ProdCard" key={index}>
                <img src={proObj.productImage} alt={proObj.productName} />
                <div className="cardInfo">
                  <span className="name">{proObj.productName}</span>
                  <span className="price">{proObj.productPrice}:-</span>
                </div>
            </div>
        ))}
        </div>
    }
    </div>
  );
};

export default ProductCard;