import React, { useState } from "react";

const productList = JSON.parse(localStorage.getItem('products')) || [];
const ManageProducts = () => {
  const [product, setProduct] = useState({
      productName: "",
      productPrice: "",
      productDesptn: "",
  });
  

  const handleInput = (event) => {
    setProduct((prevProducts) => ({
      ...prevProducts,
      [event.target.name]: event.target.value,
    }))
  } 

  const handleSubmit = (event) => {
    event.preventDefault();
    productList.push(product);
    localStorage.setItem('products', JSON.stringify(productList));
    window.location.reload();
  }

  const toBase64 = e =>  {
    let reader = new FileReader();
    reader.onload = function(e){
        setProduct((prevImage) => ({
            ...prevImage,
            productImage: reader.result
        }) );
    }
    reader.readAsDataURL(e.target.files[0]);
  };

  return (
    <div className="MangProduct">
      <form onSubmit={handleSubmit}>
        <div>
          <label className="InpFieldLable">
            Product name
            <br/>
            <input 
                name="productName"
                className="InputField"
                type="text"
                placeholder="Enter product name" 
                value={product.productName}
                onChange={handleInput}
                required
              />
          </label>
          <label className="InpFieldLable">
            Price
            <br/>
            <input 
                name="productPrice"
                type="text"
                placeholder="Enter Price" 
                value={product.productPrice}
                onChange={handleInput}
                required
              />
          </label>
        </div>
        <div>
        <label className="InpFieldLable">
            Product description <small>(optional)</small>
            <br/>
            <textarea 
                name="productDesptn"
                type="text"
                placeholder="Enter description" 
                value={product.productDesptn}
                onChange={handleInput}
                rows={5}
                />
          </label>
          <label className="InpFieldLable">
            Product image <small>(optional)</small>
            <br/>
            <input 
                name="productImage"
                type="file" 
                placeholder="Choose file..." 
                onChange={toBase64}
                />
          </label>
        </div>
        <button type="submit" className="submitBtn">Add Product</button>
      </form>
      <table>
      <thead>
          <tr>
            <th></th>
            <th>Name</th>
            <th className="descp">Description</th>
            <th>Price</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {productList.map((prodObj, index) => {
            const Name = prodObj.productName;
            const Price = prodObj.productPrice;
            const description = prodObj.productDesptn;
            const Image = prodObj.productImage;
            return(
              <tr key={index}>
                  <td><img src={Image} alt={Name} /></td>
                  <td><span>{Name}</span></td>
                  <td className="descp"><span>{description}</span></td>
                  <td><span>{Price}:-</span></td>
                  <td><button>Remove</button></td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

export default ManageProducts;